<template>
  <div class="page-container">
    <GeneratorMenu class="menuBlock" :onSubmit="onGenerate" />
    <BannersList
      :generatedBanners="generatedBannersStore.banners"
      :onLoadMore="onLoadMore"
    />
    <NewBannersBanner class="newBannersBlock" />
    <SuggestionBanner class="suggestionBlock" />
  </div>
</template>

<script>
import GeneratorMenu from './GeneratorMenu/GeneratorMenu.vue';
import BannersList from './BannersList/BannersList.vue';
import NewBannersBanner from '@/components/banners/NewBannersBanner/NewBannersBanner.vue';
import SuggestionBanner from '@/components/banners/SuggestionBanner/SuggestionBanner.vue';
import { useAppStore } from '@/stores/useAppStore';
import { useGeneratedBannersStore } from '@/stores/useGeneratedBannersStore';
import { useToaster } from '@/utils/useToaster';

export default {
  data: () => ({
    banners: [],
  }),
  setup() {
    return {
      appStore: useAppStore(),
      toaster: useToaster(),
      generatedBannersStore: useGeneratedBannersStore(),
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    async getGeneratedBanners() {
      try {
        await this.generatedBannersStore.getGeneratedBanners();
      } catch (error) {
        console.error(error);
        this.toaster.error('Не получилось загрузить баннеры :(');
      }
    },
    async init() {
      this.appStore.toggleAppLoader(true);

      await this.getGeneratedBanners();

      setTimeout(() => {
        this.appStore.toggleAppLoader(false);
      }, 135);
    },
    async onGenerate(params, isLoadMore = false) {
      try {
        await this.generatedBannersStore.findGeneratedBanners(
          {
            ...params,
            page: params.page ?? 0,
          },
          isLoadMore,
        );

        if (!isLoadMore) {
          document.getElementById('bannersList').scrollIntoView({
            behavior: 'smooth',
            block: 'start',
          });
        }
      } catch (error) {
        console.error(error);
        this.toaster.error('Не получилось найти баннеры :(');
      }
    },
    async onLoadMore() {
      await this.onGenerate(
        {
          ...this.generatedBannersStore.oldParams,
          page: this.generatedBannersStore.currentPage + 1,
        },
        true,
      );
    },
  },
  components: {
    GeneratorMenu,
    BannersList,
    NewBannersBanner,
    SuggestionBanner,
  },
};
</script>

<style lang="scss" scoped>
.page-container {
  margin: 0 100px;
  .menuBlock {
    margin-bottom: 44px;
  }

  .suggestionBlock {
    margin-bottom: 75px;
  }

  .newBannersBlock {
    margin-bottom: 15px;
  }
}
</style>
